import React, { useState, useLayoutEffect } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import ShareIcon from '@material-ui/icons/Share'
import QRCode from 'qrcode'
import { makeStyles } from '@material-ui/core/styles'
import { Paper } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
  root: {
  },
  title: {
    textAlign: 'center'
  },
  qrPaper: {
    padding: theme.spacing(2)
  },
  qrCode: {
    width: '100%',
    height: 400
  }
}))

const QRDisplay = ({ onClose, open, series, activePhoto }) => {
  const classes = useStyles()
  const [qrCodeEl, setQRCodeEl] = useState(null)
  if (!series) return null
  const obj = { type: 'series', s: series.id, p: series.photos[activePhoto].id }
  const encodedId = encodeURI(JSON.stringify(obj))
  const url = `${window.location.origin}${process.env.PUBLIC_URL}/?fromqr=${encodedId}`
  console.log('SOCIAL URL', process.env.PUBLIC_URL)

  useLayoutEffect(() => {
    QRCode.toString(url, (err, svgStr) => {
      if (err) console.error(err)
      else setQRCodeEl(svgStr)
    })
  }, [series])

  const hasShare = Boolean(navigator.share)
  const share = () => {
    if (navigator.share) {
      navigator.share({
        title: series.name,
        text: series.note || 'MorPhoto Series',
        url: url
      })
        .then(() => console.log('Successful share'))
        .catch((error) => console.log('Error sharing', error))
    }
  }

  return (
    <Dialog
      open={open}
      fullWidth

      onClose={onClose}
      className={classes.root
      }>
      <Paper className={classes.qrPaper}>
        <Typography variant='subtitle1' component='div' className={classes.title}>{series.name}</Typography>
        {['OPEN', 'PUBLIC'].indexOf(series.access) > -1
          ? <svg className={classes.qrCode} dangerouslySetInnerHTML={{ __html: qrCodeEl }} />
          : <Typography variant='h4' color='error'>You cannot share a private series.</Typography>
        }
        <div>
          {hasShare && (
            <IconButton className={classes.button} aria-label='Delete' onClick={share}>
              <ShareIcon />
            </IconButton>
          )}
        </div>
        {series.coords && <Typography component='div' className={classes.title}>[ {series.coords[0]}, {series.coords[1]} ]</Typography>}
      </Paper>
    </Dialog>
  )
}

QRDisplay.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  series: PropTypes.object.isRequired,
  activePhoto: PropTypes.number
}

export default QRDisplay
